          <el-input v-model="ruleForm.yuangongxingming" placeholder="" clearable
                    :readonly="ro.yuangongxingming"></el-input>
        </el-form-item>
        <el-form-item
            :style='{"padding":"2px 0","margin":"0 auto 24px","color":"inherit","borderRadius":"4px","background":"#f9f9f9","width":"60%","fontSize":"inherit","height":"auto"}'
            v-else class="input" label="" prop="yuangongxingming">
          <el-input v-model="ruleForm.yuangongxingming" placeholder="" readonly></el-input>
        </el-form-item>
        <el-form-item
            :style='{"padding":"2px 0","margin":"0 auto 24px","color":"inherit","borderRadius":"4px","background":"#f9f9f9","width":"60%","fontSize":"inherit","height":"auto"}'
