<script>
// url
import {isNumber, isIntNumer, isEmail, isPhone, isMobile, isURL, checkIdCard} from "@/utils/validate";

export default {
  data() {
    let self = this
    var validateIdCard = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!checkIdCard(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateUrl = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!isURL(value)) {
        callback(new Error("URL"));
      } else {
        callback();
      }
    };
    var validateMobile = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!isMobile(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validatePhone = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!isPhone(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateEmail = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!isEmail(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateNumber = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!isNumber(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateIntNumber = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!isIntNumer(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      id: '',
      type: '',


      ro: {
        qingjiabianhao: false,
        qingjialeixing: false,
        qingjiashiyou: false,
        qingjiatianshu: false,
        qingjiashijian: false,
        yuangonggonghao: false,
        yuangongxingming: false,
        zhuguangonghao: false,
        sfsh: false,
        shhf: false,
      },


      ruleForm: {
        qingjiabianhao: this.getUUID(),
        qingjialeixing: '',
        qingjiashiyou: '',
        qingjiatianshu: '',
        qingjiashijian: '',
        yuangonggonghao: '',
        yuangongxingming: '',
        zhuguangonghao: '',
        shhf: '',
      },

      qingjialeixingOptions: [],


      rules: {
        qingjiabianhao: [],
        qingjialeixing: [],
        qingjiashiyou: [],
        qingjiatianshu: [
          {validator: validateIntNumber, trigger: 'blur'},
        ],
        qingjiashijian: [],
        yuangonggonghao: [],
        yuangongxingming: [],
        zhuguangonghao: [],
        sfsh: [],
        shhf: [],
      }
    };
  },
  props: ["parent"],
  computed: {},
  components: {},
  created() {
    this.ruleForm.qingjiashijian = this.getCurDateTime()
  },
  methods: {

    // 
    download(file) {
      window.open(`${file}`)
    },
    // 
    init(id, type) {
      if (id) {
        this.id = id;
        this.type = type;
      }
      if (this.type == 'info' || this.type == 'else') {
        this.info(id);
      } else if (this.type == 'logistics') {
        this.logistics = false;
        this.info(id);
      } else if (this.type == 'cross') {
        var obj = this.$storage.getObj('crossObj');
        for (var o in obj) {
          if (o == 'qingjiabianhao') {
            this.ruleForm.qingjiabianhao = obj[o];
            this.ro.qingjiabianhao = true;
            continue;
          }
          if (o == 'qingjialeixing') {
            this.ruleForm.qingjialeixing = obj[o];
            this.ro.qingjialeixing = true;
            continue;
          }
          if (o == 'qingjiashiyou') {
            this.ruleForm.qingjiashiyou = obj[o];
            this.ro.qingjiashiyou = true;
            continue;
          }
          if (o == 'qingjiatianshu') {
            this.ruleForm.qingjiatianshu = obj[o];
            this.ro.qingjiatianshu = true;
            continue;
          }
          if (o == 'qingjiashijian') {
            this.ruleForm.qingjiashijian = obj[o];
            this.ro.qingjiashijian = true;
            continue;
          }
          if (o == 'yuangonggonghao') {
            this.ruleForm.yuangonggonghao = obj[o];
            this.ro.yuangonggonghao = true;
            continue;
          }
          if (o == 'yuangongxingming') {
            this.ruleForm.yuangongxingming = obj[o];
            this.ro.yuangongxingming = true;
            continue;
          }
          if (o == 'zhuguangonghao') {
            this.ruleForm.zhuguangonghao = obj[o];
            this.ro.zhuguangonghao = true;
            continue;
          }
        }
      }


      // 
      this.$http({
        url: `${this.$storage.get('sessionTable')}/session`,
        method: "get"
      }).then(({data}) => {
        if (data && data.code === 0) {

          var json = data.data;
          if (((json.yuangonggonghao != '' && json.yuangonggonghao) || json.yuangonggonghao == 0) && this.$storage.get("role") != "") {
            this.ruleForm.yuangonggonghao = json.yuangonggonghao
            this.ro.yuangonggonghao = true;
          }
          if (((json.yuangongxingming != '' && json.yuangongxingming) || json.yuangongxingming == 0) && this.$storage.get("role") != "") {
            this.ruleForm.yuangongxingming = json.yuangongxingming
            this.ro.yuangongxingming = true;
          }
          if (((json.zhuguangonghao != '' && json.zhuguangonghao) || json.zhuguangonghao == 0) && this.$storage.get("role") != "") {
            this.ruleForm.zhuguangonghao = json.zhuguangonghao
            this.ro.zhuguangonghao = true;
          }
        } else {
          this.$message.error(data.msg);
        }
      });

      this.qingjialeixingOptions = ",,,,,".split(',')

    },
    // 

    info(id) {
      this.$http({
        url: `yuangongqingjia/info/${id}`,
        method: "get"
      }).then(({data}) => {
        if (data && data.code === 0) {
          this.ruleForm = data.data;
          //
          let reg = new RegExp('../../../upload', 'g')//g
        } else {
          this.$message.error(data.msg);
        }
      });
    },


    // 
    onSubmit() {
      if (this.ruleForm.qingjiabianhao) {
        this.ruleForm.qingjiabianhao = String(this.ruleForm.qingjiabianhao)
      }


      var objcross = this.$storage.getObj('crossObj');

      //
      var crossuserid;
      var crossrefid;
      var crossoptnum;
      if (this.type == 'cross') {
        var statusColumnName = this.$storage.get('statusColumnName');
        var statusColumnValue = this.$storage.get('statusColumnValue');
        if (statusColumnName != '') {
          var obj = this.$storage.getObj('crossObj');
          if (statusColumnName && !statusColumnName.startsWith("[")) {
            for (var o in obj) {
              if (o == statusColumnName) {
                obj[o] = statusColumnValue;
              }
            }
            var table = this.$storage.get('crossTable');
            this.$http({
              url: `${table}/update`,
              method: "post",
              data: obj
            }).then(({data}) => {
            });
          } else {
            crossuserid = this.$storage.get('userid');
            crossrefid = obj['id'];
            crossoptnum = this.$storage.get('statusColumnName');
            crossoptnum = crossoptnum.replace(/\[/, "").replace(/\]/, "");
          }
        }
      }
      this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          if (crossrefid && crossuserid) {
            this.ruleForm.crossuserid = crossuserid;
            this.ruleForm.crossrefid = crossrefid;
            let params = {
              page: 1,
              limit: 10,
              crossuserid: this.ruleForm.crossuserid,
              crossrefid: this.ruleForm.crossrefid,
            }
            this.$http({
              url: "yuangongqingjia/page",
              method: "get",
              params: params
            }).then(({
                       data
                     }) => {
              if (data && data.code === 0) {
                if (data.data.total >= crossoptnum) {
                  this.$message.error(this.$storage.get('tips'));
                  return false;
                } else {
                  this.$http({
                    url: `yuangongqingjia/${!this.ruleForm.id ? "save" : "update"}`,
                    method: "post",
                    data: this.ruleForm
                  }).then(({data}) => {
                    if (data && data.code === 0) {
                      this.$message({
                        message: "",
                        type: "success",
                        duration: 1500,
                        onClose: () => {
                          this.parent.showFlag = true;
                          this.parent.addOrUpdateFlag = false;
                          this.parent.yuangongqingjiaCrossAddOrUpdateFlag = false;
                          this.parent.search();
                          this.parent.contentStyleChange();
                        }
                      });
                    } else {
                      this.$message.error(data.msg);
                    }
                  });

                }
              } else {
              }
            });
          } else {
            this.$http({
              url: `yuangongqingjia/${!this.ruleForm.id ? "save" : "update"}`,
              method: "post",
              data: this.ruleForm
            }).then(({data}) => {
              if (data && data.code === 0) {
                this.$message({
                  message: "",
                  type: "success",
                  duration: 1500,
                  onClose: () => {
                    this.parent.showFlag = true;
                    this.parent.addOrUpdateFlag = false;
                    this.parent.yuangongqingjiaCrossAddOrUpdateFlag = false;
                    this.parent.search();
                    this.parent.contentStyleChange();
                  }
                });
              } else {
                this.$message.error(data.msg);
              }
            });
          }
        }
      });
    },
    // uuid
    getUUID() {
      return new Date().getTime();
    },
    // 
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.yuangongqingjiaCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
  }
};
</script>
